import React from 'react'
import Church from '../images/church.png'
import Confetti from '../images/confetti.png'
import Dance from '../images/dance.png'
import Meal from '../images/meal.png'
import RomanticDinner from '../images/romantic-dinner.png'
import WeddingArch from '../images/wedding-arch.png'
import WeddingCake from '../images/wedding-cake.png'
import Wine from '../images/wine.png'
import '../styles/timeline.css'
import Divider from './divider'

export default function Timeline() {
    return (
        <section class="timeline">
            <div>
            <h2>Program</h2>
            <Divider/>
                <div className="timeline">
                    <div className="timeline__event  animated fadeInUp delay-3s timeline__event">
                        <div className="timeline__event__icon ">
                            <i className="lni-cake" />
                            <div className="icon">
                                <img className="icon-img" src={Church} />
                            </div>
                        </div>
                        <div className="timeline__event__date">15:00</div>
                        <div className="timeline__event__content ">
                            <div className="timeline__event__title">Templomi szertartás<br/> <span>Szent Anna körtemplom</span></div>
                        </div>
                    </div>
                    <div className="timeline__event animated fadeInUp delay-2s timeline__event">
                        <div className="timeline__event__icon">
                            <i className="lni-burger" />
                            <div className="icon">
                                <img className="icon-img" src={Meal} />
                            </div>
                        </div>
                        <div className="timeline__event__date">16:00</div>
                        <div className="timeline__event__content">
                            <div className="timeline__event__title">Vendégvárás <br/> <span>Aranyhalas tanya</span></div>
                        </div>
                    </div>
                    <div className="timeline__event animated fadeInUp delay-1s timeline__event">
                        <div className="timeline__event__icon">
                            <i className="lni-slim" />
                            <div className="icon">
                                <img className="icon-img" src={WeddingArch} />
                            </div>
                        </div>
                        <div className="timeline__event__date">16:45</div>
                        <div className="timeline__event__content">
                            <div className="timeline__event__title">Polgári szertartás</div>
                        </div>
                    </div>
                    <div className="timeline__event animated fadeInUp timeline__event">
                        <div className="timeline__event__icon">
                            <i className="lni-cake" />
                            <div className="icon">
                                <img className="icon-img" src={Wine} />
                            </div>
                        </div>
                        <div className="timeline__event__date">17:30</div>
                        <div className="timeline__event__content">
                            <div className="timeline__event__title">gratuláció, fotózás</div>
                        </div>
                    </div>
                    <div className="timeline__event animated fadeInUp delay-1s timeline__event">
                        <div className="timeline__event__icon">
                            <i className="lni-slim" />
                            <div className="icon">
                                <img className="icon-img" src={RomanticDinner} />
                            </div>
                        </div>
                        <div className="timeline__event__date">18:30</div>
                        <div className="timeline__event__content">
                            <div className="timeline__event__title">Vacsora</div>
                        </div>
                    </div>
                    <div className="timeline__event animated fadeInUp timeline__event">
                        <div className="timeline__event__icon">
                            <i className="lni-cake" />
                            <div className="icon">
                                <img className="icon-img" src={Dance} />
                            </div>
                        </div>
                        <div className="timeline__event__date">20:00</div>
                        <div className="timeline__event__content">
                            <div className="timeline__event__title">Nyitótánc</div>
                        </div>
                    </div>
                    <div className="timeline__event animated fadeInUp delay-1s timeline__event">
                        <div className="timeline__event__icon">
                            <i className="lni-slim" />
                            <div className="icon">
                                <img className="icon-img" src={WeddingCake} />
                            </div>
                        </div>
                        <div className="timeline__event__date">22:00</div>
                        <div className="timeline__event__content">
                            <div className="timeline__event__title">Torta</div>
                        </div>
                    </div>
                    <div className="timeline__event animated fadeInUp timeline__event">
                        <div className="timeline__event__icon">
                            <i className="lni-cake" />
                            <div className="icon">
                                <img className="icon-img" src={Confetti} />
                            </div>
                        </div>
                        <div className="timeline__event__date">23:00</div>
                        <div className="timeline__event__content">
                            <div className="timeline__event__title">Menyecske tánc <br/> <span>pusziért / jókivánságért</span></div>
                        </div>
                    </div>
                </div>
            </div>

        </section>
    )
}
