import React from 'react'
import Divider from './divider'
import Acc from '../images/accomodation.png'
import Parking from '../images/parking.png'

export default function Info() {
    return (
        <section className="info">
            <h2>Infók</h2>
            <Divider/>
            <div className="accom">
                <div className="accom-item">
                    <h4>Parkolás</h4>
                    <p>A Szent Anna körtemplom oldalában a Rudnay Sándor téren található egy nagy parkoló.</p>
                    <p>Az Aranyhalas tanyának saját belső parkolója van.</p>
                </div>
                <div className="accom-item">
                    <h4>Szállás</h4>
                    <p>A szállást mi biztosítjuk. Kérjük, ha mégsincs rá szükséged, jelezd nekünk.</p>
                    <p><b>Családtagoknak: </b>Márta Panzió - 2500 Esztergom, Bocskoroskúti út 1.</p>
                    <p><b>Barátoknak: </b>Rugby Club Hostel - 2500 Esztergom, Nagy-Duna sétány 1.</p>
                </div>
            </div>
        </section>
    )
}
